import { injectGlobal } from 'styled-components';

/* eslint-disable */
injectGlobal`
@import url('https://fonts.googleapis.com/css?family=Aldrich');

body {
  font-family: 'Aldrich', sans-serif;
  font-size: 25px;
}

h1 {
  color: white;
}

h2 {
  color: white;
}
`;



